	module.exports = {"data":{"markdownRemark":{"html":"<h2>Figma</h2>\n<p>Figma is an interface design tool based in the browser, making it easier for teams to create software. It connects to the cloud, allowing for multiple designers to work on the same project simultaneously.</p>\n<p>It has an in-built version control system, like Git, which allows designers to design without the fear of designs being overridden or deleted by fellow team-mates. </p>\n<h3>Why Figma?</h3>\n<ul>\n<li>Browser-based</li>\n<li>The desktop app is available for both Mac and Windows (unlike Sketch)</li>\n<li>In-built versioning</li>\n<li>You can import your Sketch files</li>\n<li>Strong Typography and Vector Graphic Tools</li>\n<li>It has a Multiplayer Mode which makes collaboration a breeze</li>\n</ul>\n<p>The Figma UI is very easy* to use for beginners. If you have been a former Sketch user, you will feel home in no time. </p>\n<p>*If you have not used Sketch before, it might take a while for you to familiarise.</p>\n<h4>More Information</h4>\n<p>The Figma Website - <a href='https://figma.com' target='_blank' rel='nofollow'>Figma</a></p>\n<p>Figma vs Sketch: The Showdown - <a href='https://www.smashingmagazine.com/2017/03/sketch-figma-showdown/' target='_blank' rel='nofollow'>smashingmagazine</a></p>\n<p>Another smackdown, Figma vs Sketch - <a href='https://medium.com/@mengto/figma-vs-sketch-c01e5e74eddd' target='_blank' rel='nofollow'>Medium</a></p>\n<p>Need some tutorials? Check out the official video series by Figma: <a href='https://www.youtube.com/channel/UCQsVmhSa4X-G3lHlUtejzLA' target='_blank' rel='nofollow'>Youtube</a></p>","fields":{"slug":"/designer-tools/Figma/"},"frontmatter":{"title":"Figma"}}},"pathContext":{"slug":"/designer-tools/Figma/"}}

